<template>
	<view>
		<view class="u-page" v-if="comments.length || hotComments.length">
			<u-divider text="热评" :hairline="true"></u-divider>
			<!-- HotComment -->
			<view class="album" v-for="(item, index) in hotComments">
				<view class="album__avatar">
					<image
							:src="item.user.avatarUrl"
							mode=""
							style="width: 32px;height: 32px;"
					></image>
				</view>
				<view class="album__content">
					<u--text
							:text="item.user.nickname"
							type="primary"
							bold
							size="16"
					></u--text>
					<u--text
							margin="0 0 8px 0"
							:text="item.timeStr"
							size="12"
					></u--text>
					<u--text
							margin="0 0 8px 0"
							:text="item.content"
					></u--text>
					<u-album :urls="urls1" keyName="src2"></u-album>
					<u--text
						prefixIcon="heart"
						:text="item.likedCount"
						size=12
						margin="8px 0 16px 0"
						color="#bfbfbf" 
						iconStyle="color: #bfbfbf;"
						>
					</u--text>
				</view>
			</view>
			<u-divider :hairline="true"></u-divider>
			<!-- Comment -->
			<view class="album" v-for="(item, index) in comments">
				<view class="album__avatar">
					<image
							:src="item.user.avatarUrl"
							mode=""
							style="width: 32px;height: 32px;"
					></image>
				</view>
				<view class="album__content">
					<u--text
							:text="item.user.nickname"
							type="primary"
							bold
							size="16"
					></u--text>
					<u--text
							margin="0 0 8px 0"
							:text="item.timeStr"
							size="12"
					></u--text>
					<u--text
							margin="0 0 8px 0"
							:text="item.content"
					></u--text>
					<u-album :urls="urls1" keyName="src2"></u-album>
					<u--text
						prefixIcon="heart"
						:text="item.likedCount"
						size=12
						margin="0px 0 16px 0"
						color="#bfbfbf" 
						iconStyle="color: #bfbfbf;"
						>
					</u--text>
				</view>
			</view>
		</view>
		<u-empty
			mode="data"
			icon="http://cdn.uviewui.com/uview/empty/data.png"
			v-else
		>
		</u-empty>
		<!-- 加载页 -->
		<u-loading-page
			:loadingText="loadingPageData.loadingText"
			:loadingMode="loadingPageData.loadingMode"
			:bgColor="loadingPageData.bgColor"
			:color="loadingPageData.color"
			:loading="loading"
			:loadingColor="loadingPageData.loadingColor"
			style="z-index: 999;">
		</u-loading-page>
	</view>
</template>

<script>
	import { getSongComment } from '@/http/http.api.js';
	export default {
		data() {
			return {
				// 加载页
				loading: false,
				loadingPageData: {
					loadingText: '正在搜索',
					loadingMode: 'semicircle',
					bgColor: 'rgba(0, 0, 0, 0.3)',
					color: '#ffffff',
					loadingColor: '#ffffff'
				},
				
				playInfo: this.$zaudio.playinfo,
				
				urls1: [],
				
				offset: 1,
				limit: 10,
				
				songId: 0,
				
				comments: [],
				hotComments: []
			};
		},
		methods:{
			getComments(){
				this.$zaudio.audiolist.forEach((item, index) => {
					if(this.$zaudio.playinfo.src === item.src){
						this.songId = this.$zaudio.audiolist[index].id;
						console.log(this.songId);
						getSongComment({ params: { id: this.songId, limit: this.limit, offset: (this.offset - 1) * this.limit } }).then(res => {
							// console.log((res.data.comments))
							// this.$u.vuex('vuex_comments', this.comments.concat(res.data.comments));
							// this.$u.vuex('vuex_hotComments', this.hotComments.concat(res.data.hotComments));
							this.comments = res.data.comments;
							this.hotComments = res.data.hotComments;
							this.loading = false;
						}).catch(e => {
							console.log(e);
						});
					};
					if(this.$zaudio.audiolist.length === index + 1){
						console.log(index + 1)
						this.loading = false;
					}
				});
			}
		},
		watch:{
			playInfo: {
				handler(newV, oldV) {
						// this.loading = true;
						// this.getComments();
				},
				deep: true
			}
		},
		onLoad() {
			// this.$u.toast('加载中...');
			this.getComments();
		},
		onShow() {
			// this.$zaudio.on('canPlay', 'event-a', data => {
			// 	this.loading = true;
			// 	this.getComments();
			// });
			
			this.$zaudio.on('ended', 'event-b', data => {
				this.loading = true;
				this.getComments();
			});
		},
		onUnload() {
			this.$zaudio.off('canPlay', 'event-a');
			this.$zaudio.off('ended', 'event-b');
		}
	}
</script>

<style lang="scss">
	.u-page{
		padding: 20px;
	}
	
	.album {
		@include flex;
		align-items: flex-start;

		&__avatar {
			 background-color: $u-bg-color;
			 padding: 5px;
			 border-radius: 3px;
		 }
	
		&__content {
			 margin-left: 10px;
			 flex: 1;
		 }
	}
</style>
